import React, { Component } from 'react';

import {NavLink} from 'react-router-dom';

class RegisterForm extends Component{

    constructor(props){
        super(props);
        this.state={
        register:{
            firstName:'',
            lastName:'',
            emailId:'',
            password:'',
            dateOfBirth:'',
            gender:'',
            hobbies:[],
            address:'',
            city:'',
        },
        error:{
            firstName : false,
            lastName: false,
            emailId: false, 
            password: false,  
            dateOfBirth: false,
            gender: false,
            hobbies: false,
            address: false,
            city: false,   
        } 
                   

        }
    }

    onHandleInput = (event)=>{
     //console.log(event.target.type)
     if(event.target.type === "checkbox"){
         if(event.target.checked){
            this.state.register.hobbies.push(event.target.value);
         }
         else{
            const index = this.state.register.hobbies.findIndex((value,index)=> {
                return value === event.target.value
            });
            this.state.register.hobbies.splice(index,1);   
         }
         this.setState({
             register:{...this.state.register, hobbies : this.state.register.hobbies}
         })
       
     }
     else{
        this.setState({
            register:{...this.state.register, [event.target.name] : event.target.value}
          })
     }
       
    }
     
    regForm(){
        for(let key in this.state.register){
            if(key === 'hobbies'){
                if(this.state.register.hobbies.length === 0){
                    this.state.error.hobbies = true;
                }else{
                    this.state.error.hobbies = false;
                }
            }
            else if(this.state.register[key] ===''){
                this.state.error[key]= true;
            }
            else{
                this.state.error[key]=false;
            }
        }
        
        this.setState({
            error: this.state.error
        })
    }

    
    onInputFocus = (event) => {
    
       this.setState({
        error:{...this.state.error, [event.target.name]: false}
       })
    }

    onInputBlur = (event) => {
       
        const value = event.target.value;
        if(value.length===0){
            this.setState({
                error:{...this.state.error,[event.target.name]: true}
            })
        }
    }

    render(){
        return(
            <div className="loginform">
                <h1>REGISTERATION FORM</h1>
                <div>
                    <label className="label">First Name</label>
                    <input type="text" className="input"  name="firstName" placeholder="enter your first name.." onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}  />
                    {this.state.error.firstName && 
                    <div>
                    <span className="error-msg">Please enter a valid first name</span>
                    </div>
                    }
                </div>
                <div className="m-top-20">
                    <label className="label" >Last Name</label>
                    <input type="text" className="input" name="lastName" placeholder="enter your Last name.." onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    {this.state.error.lastName && 
                    <div>
                    <span className="error-msg"> Please enter a valid last name</span>
                    </div>
                    }
                </div>
                <div className="m-top-20">
                    <label className="label">Email Id</label>
                    <input type="text" className="input" name="emailId" placeholder="enter your email id.." onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    {this.state.error.emailId && 
                    <div>
                    <span className="error-msg"> Please enter a valid email id</span>
                    </div>
                    }
                </div>
                <div className="m-top-20">
                    <label className="label">Password</label>
                    <input type="password" className="input" name="password" placeholder="enter your password.." onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    {this.state.error.password && 
                    <div>
                    <span className="error-msg">please enter a vaild password</span>
                    </div>
                    }
                </div>
                <div className="m-top-20">
                    <label className="label">D.O.B</label>
                    <input type="date" name="dateOfBirth" className="input" onChange={this.onHandleInput}/>
                    {this.state.error.dateOfBirth && 
                    <div>
                    <span className="error-msg">please enter a vaild date of birth</span>
                    </div>
                    }
                    </div>
                <div className="m-top-20">
                    <label className="label"> Gender</label>
                    <input type="radio" name="gender" value="male" onChange={this.onHandleInput}/> Male
                    <input type="radio" name="gender" value="female" onChange={this.onHandleInput}/> Female
                    <input type="radio" name="gender" value="do not wish to specify"onChange={this.onHandleInput}/> Others
                    {this.state.error.gender && 
                    <div>
                    <span className="error-msg">please select any gender</span>
                    </div>
                    }
                </div>
                <div className="m-top-20">
                    <label className="label"> Hobbies</label>
                    <input type="checkbox" value="cricket" className="m-left-10" name="hobbies" onChange={this.onHandleInput} /> Cricket
                    <input type="checkbox" value="football" className="m-left-10" name="hobbies" onChange={this.onHandleInput} /> Football
                    <input type="checkbox" value="hockey" className="m-left-10" name="hobbies" onChange={this.onHandleInput} /> Hockey
                    <input type="checkbox" value="tennis" className="m-left-10" name="hobbies" onChange={this.onHandleInput} /> Tennis
                    {this.state.error.hobbies && 
                    <div>
                    <span className="error-msg">please select hobbies</span>
                    </div>
                    }
                </div>  
                <div className="m-top-20">
                    <label className="label">Address</label>
                    <textarea placeholder="Enter Your Address"  name="address" className="text-box" onChange={this.onHandleInput}onFocus={this.onInputFocus} onBlur={this.onInputBlur}></textarea>
                    {this.state.error.address && 
                    <div>
                    <span className="error-msg">please enter vaild address</span>
                    </div>
                    }
                </div>  
                <div className="m-top-20">
                    <label className="label">City</label> 
                    <select className="input" defaultValue="" onChange={this.onHandleInput} name="city">
                        <option value="" disabled>Please Select Any City</option>
                        <option value="CH">Chennai</option>
                        <option value="BLORE">Banglore</option>
                        <option value="HYD">Hyderabad</option>
                        <option value="BOM">Mumbai</option>
                        <option value="PU">Pune</option>
                        <option value="KO">Cochin</option>
                        <option value="GO">Goa</option>
                        <option value="VI">Vizag</option>
                    </select>
                    {this.state.error.city && 
                    <div>
                    <span className="error-msg">please enter vaild city</span>
                    </div>
                    }  
                    </div>
                    <div className="m-top-20">
                        <button onClick={() => {this.regForm();}}>Submit My Registeration Form</button>
                    </div>
                    <NavLink to="/">Go Back to Login Page</NavLink>
            </div>
        );
    }
} 
export default RegisterForm;